import { Component } from '@angular/core';
import {AlertController, LoadingController , IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-solprestamo',
  templateUrl: 'solprestamo.html',
})
export class SolprestamoPage {

  public loading: boolean = true;

  items: any=[];
  items2: any=[];
  generated = '';
  permisom: any=[];

  idafiliado:any='na';
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
  idempresa:any;
  pingreso:boolean=false;
  pconsulta:boolean=false;
  peliminacion:boolean=false;
  pcreacion:boolean=false;
  pactualizacion:boolean=false;
  preporte:boolean=false;
  pfiltro:boolean=false;
//private barcodeScanner:BarcodeScanner,
  constructor(public loadingCtrl: LoadingController, public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth


    ) {

      this.permisom = String(navParams.get("permiso")).split(",");
      this.pingreso=(  this.permisom[0] == 'true');
      this.pconsulta=(  this.permisom[1] == 'true');
      this.peliminacion=(  this.permisom[2] == 'true');
      this.pcreacion=(  this.permisom[3] == 'true');
      this.pactualizacion=(  this.permisom[4] == 'true');
      this.pfiltro=(  this.permisom[5] == 'true');
      this.preporte=(  this.permisom[6] == 'true');




  }

  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()
  {}



  pideregistros() {
      let self = this;
      self.loading = true;
      this.regService.getReg('todos/'+this.idempresa+'/'+this.username,'solprestamos').then((data) => {
        this.items=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })
  });
  }
  ngOnInit() {

  }
  ionViewDidLoad() {

    this.storage.get('username').then((value) => {
      this.username = value;


   this.translateService.get('APPX').subscribe((value) => {
    this.idempresa=value
    this.pideregistros();

})

});

  }

  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operación?',
    buttons: [
      {text: 'Si',
        handler: () => {
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'solprestamos').then((data) => {
            this.pideregistros();
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });


        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
  }

  updateRecord(item : any,no) : void
   {

    let addModal = this._MODAL .create('SolprestamoCreatePage',{ record : item,cui:item.cui,carne:item.carne , actualizacion:true},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros();
      }
    })
    addModal.present();



   }
   goBack() {
    this.navCtrl.pop();
  }
   addRecord() : void
   {


    let prompt = this.alertCtrl.create({
      title:' Validando información',
      message: ' Ingresa REGISTRO PERSONAL y CUI Registrado , para validar información:',
      inputs: [
        {
          name: 'carne',
          placeholder:'Ingrese REGISTRO PERSONAL USAC',
          value:''//this.nov

        },
        {
          name: 'cui',
          placeholder:'Ingrese CUI registrado',
          value:''//this.cuitt
        }
      ],
      buttons: [
        {
          text: 'Cancelar'
        },
        {
          text: 'Aceptar',
          handler: todo => {

                if(todo){

           if(todo.carne==''  ||  todo.cui=='' )
           {
            this.msj.showError3('Advertencia','Ingrese un REGISTRO PERSONAL o CUI valido');
            return;

           }



          const loading = this.loadingCtrl.create({
            spinner: 'bubbles',
            content: "Procesando información..."
          });
          loading.present();

          loading.dismiss();
          let addModal = this._MODAL .create('SolprestamoCreatePage',{ record : null,cui:todo.cui,carne:todo.carne , actualizacion:true},{showBackdrop: true,enableBackdropDismiss: false});
          addModal.onDidDismiss(data => {
            if (data) {
              this.pideregistros();
            }
          })
          addModal.present();
         }







                }


          }

      ]
    });

    prompt.present();



  }


}



